.social {
  position: relative;
}

.social__list::after {
  content: "";
  position: absolute;
  background-color: $silver;
  left: calc(50% - 130px);
  bottom: 22px;
  width: 260px;
  height: 1px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 24px;
  padding: 0;
  list-style: none;

  @media (min-width: $tablet-width) {
    margin-top: 3px;
    justify-content: center;
  }
}

.social__item {
  display: flex;

  @media (min-width: $tablet-width) {
    margin: 20px;
    margin-top: 0;
  }
}

.social__picture:hover,
.social__picture:focus {
  fill: $green-active;
}

.social__picture:active {
  fill: $green-active;
  opacity: 0.3;
}
