.modal {
  display: none;
}

.modal-wrapper {
  display: none;
}

.modal-wrapper--active {
  display: block;
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: $white;
  opacity: 0.9;
}

.modal-show {
  display: block;
  position: absolute;
  z-index: 2;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  left: calc(50% - 160px);
  top: 36%;
  max-width: 320px;
  background-color: $white;
  text-align: center;
  padding: 28px 30px 46px 30px;

  @media (min-width: $tablet-width) {
    max-width: 566px;
    top: 37%;
    left: calc(50% - 283px);
    padding: 37px 105px 46px 105px;
  }

  @media (min-width: $desktop-width) {
    top: 34%;
  }
}

.modal__title {
  margin: 0;

  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 57px;
  }
}

.modal__choice {
  font-size: 15px;
  color: $grey;

  @media (min-width: $tablet-width) {
    font-size: 17px;
  }
}

.modal__list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.modal__button {
  min-width: 82px;
  padding: 5px;
  border: 2px solid rgba(68, 68, 68, 0.1);
  color: $grey;
  background-color: inherit;

  @media (min-width: $tablet-width) {
    min-width: 112px;
  }
}

.modal__button:hover,
.modal__button:focus {
  background-color: inherit;
  color: inherit;
  border: 2px solid rgba(68, 68, 68, 0.3);
}

.modal__button:active {
  border: 2px solid $aquamarine;
}

.modal__button--active {
  border: 2px solid $light-green;
}

.modal__link {
  display: block;
  margin-top: 15px;
  padding-top: 18px;
  width: 100%;
  border: none;

  text-transform: uppercase;
  line-height: 16px;

  color: $white;
  background-color: $light-green;
}
