.page-main__name {
  position: relative;
  margin: 0;
  min-height: 177px;

  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 27px;
  font-weight: 700;
  text-align: center;

  background-color: $steel;

  @media (min-width: $tablet-width) {
    margin-top: -80px;
    min-height: 226px;
    padding-top: 25px;
    margin-bottom: 5px;

    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    min-height: 255px;
    margin-top: -70px;
    margin-bottom: 0;
    padding-top: 5px;

    font-size: 53px;
    line-height: 57px;
  }
}

.page-main__name--form {
  @media (min-width: $tablet-width) {
    margin-bottom: 10px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 5px;
  }
}

.page-main__catalog {
  margin-top: 30px;
  padding: 0 30px;

  @media (min-width: $tablet-width) {
    margin-top: 80px;
    padding: 0 50px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 35px;
  }
}

.page-main__production {
  margin: 0 30px;
  margin-bottom: 60px;

  @media (min-width: $tablet-width) {
    margin: 0 50px;
    margin-bottom: 0;
  }
}

.page-main__catalog::before,
.page-main__form::before {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    background-image: url("../img/zigzag-fill-tablet.svg");
    width: 100%;
    height: 24px;
    top: 300px;
    left: 0;
    border: none;
  }

  @media (min-width: $desktop-width) {
    height: 34px;
    top: 325px;
    background-image: url("../img/zigzag-fill-desktop.svg");
    background-position: center;
  }
}
